@import url(https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&family=Poppins:wght@400;500;700&display=swap);
* {
  font-family: 'Poppins', sans-serif;
  text-decoration:none;
  list-style: none;
  color: #FFFFFF;
  margin: 0;
  padding: 0;
}
img, svg {
  max-width: 100%;
  height: auto;
}
body {
  background-image: linear-gradient(to right, #d7d2cc 0%, #304352 100%);
}
/*Header*/
.header__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 130px;
  height: 50px;
}
@media (max-width: 768px) {
  .header__wrap  {
    padding: 2px 10px;
    height: 40px;
  }
}
.header__logo {
  font-weight: 700;
  font-size: 24px;
  text-decoration: none;;
  color: #FFFFFF;
}
.h-menu__wrap {
  display: flex;
}
@media (max-width: 768px) {
  .h-menu__wrap {
    flex-direction: column;
    align-items: center;
  }
}

.h-menu__item {
  margin-right: 40px;
  position: relative;
  display: block;
}
@media (max-width: 768px) {
  .h-menu__item {
    margin-right: 0;
  }
}
.h-menu__item:before, .h-menu__item:after {
  content: '';
  position: absolute;
  width: 0%;
  height: 1px;
  top: 50%;
  margin-top: -0.5px;
  background: #fff;
}

.h-menu__item:before {
  left: -2.5px;
}
.h-menu__item:after {
  right: 2.5px;
  background: #fff;
  transition: width 0.8s cubic-bezier(0.22, 0.61, 0.36, 1);
}

.h-menu__item:hover:before {
  background: #fff;
  width: 100%;
  transition: width 0.5s cubic-bezier(0.22, 0.61, 0.36, 1);
}
.h-menu__item:hover:after {
  background: transparent;
  width: 100%;
  transition: 0s;
}
.h-menu__link {
  font-size: 17px;
}
@media (max-width: 768px) {
  .h-menu__link {
    font-size: 25px;
  }
}



/*Header*/
.header__block {
  margin: 100px auto;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
}
.header__title {
  position: relative;
  margin: 0;
  font-size: 50px;
  max-width: 600px;
  z-index: 2;
}
@media (max-width: 768px) {
  .header__title {
    font-size: 28px;
  }
}
.header__paragraph {
  position: relative;
  font-size: 17px;
  margin-bottom: 40px;
  z-index: 2;
}
@media (max-width: 768px) {
  .header__paragraph {
    font-size: 15px;
  }
}
.header__btn-cv {
  position: relative;
  border-radius: 5px;
  background-color: #5C62EC;
  padding: 15px 32px;
  width: 120px;
  cursor: pointer;
  z-index: 2;
}
.header__btn-cv:hover {
  opacity: 0.8;
}
@media (max-width: 768px) {
  .header__btn-cv {
    padding: 10px 20px;
  }
}
.header__square {
  position: relative;
  display: flex;
  justify-content: center;
  margin-top: 100px;
  margin-bottom: 100px;
}
@media (max-width: 768px) {
  .header__square {
    margin-top: -50px;
    margin-bottom: -10px;
  }
}
.header__sq-1 {
  position: absolute;
  width: 444px;
  height: 444px;
  border: 1px solid #ffffff1f;
  -webkit-transform: rotate(-75deg);
          transform: rotate(-75deg);
  -webkit-animation: rotation 10s infinite cubic-bezier(0.39, 0.43, 1, 1);
          animation: rotation 10s infinite cubic-bezier(0.39, 0.43, 1, 1);
}
.header__sq-2 {
  position: absolute;
  width: 444px;
  height: 444px;
  border: 1px solid #ffffff1f;
  -webkit-transform: rotate(-75deg);
          transform: rotate(-75deg);
  -webkit-animation: rotation 10s infinite  cubic-bezier(0.6, -0.28, 0.74, 0.05);
          animation: rotation 10s infinite  cubic-bezier(0.6, -0.28, 0.74, 0.05);
}
.header__sq-3 {
  position: absolute;
  width: 444px;
  height: 444px;
  border: 1px solid #ffffff1f;
  -webkit-transform: rotate(-75deg);
          transform: rotate(-75deg);
  -webkit-animation: rotation 10s infinite cubic-bezier(0.58, 0.14, 1, 1);
          animation: rotation 10s infinite cubic-bezier(0.58, 0.14, 1, 1);
}
@media (max-width: 768px) {
  .header__sq-1,
  .header__sq-2,
  .header__sq-3 {
    display: none;
  }
}

@-webkit-keyframes rotation {
  0% {
  -webkit-transform: rotate(0);
          transform: rotate(0);
  }
  100% {
  -webkit-transform: rotate(-360deg);
          transform: rotate(-360deg);
  }
}

@keyframes rotation {
  0% {
  -webkit-transform: rotate(0);
          transform: rotate(0);
  }
  100% {
  -webkit-transform: rotate(-360deg);
          transform: rotate(-360deg);
  }
}

/* Projects  */
.section {
  padding: 70px 0;
}
@media (max-width: 768px) {
  .section {
    padding: 0;
  }
}
.projects__work {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1200px;
}
.project__title {
  margin-bottom: 60px;
  font-size: 60px;
  font-weight: 700;
  line-height: 1.3;
  color: rgb(92, 98, 236);
  text-align: center;
}
@media (max-width: 768px) {
  .project__title {
    font-size: 45px;
    margin-bottom: 40px;
  }
}
.work {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  grid-column-gap: 30px;
  -webkit-column-gap: 30px;
          column-gap: 30px;
  grid-row-gap: 30px;
  row-gap: 30px;
}
.project__work {
  max-width: 370px;
  max-height: 330px;
  background-color:#171718;
  box-shadow: var(--box-shadow);
  border-radius: 10px;
}
@media (max-width: 768px) {
  .project__work  {
    max-width: 240px;
  }
}
.project__work-title {
  padding: 15px 20px 25px;
  font-weight: 700;
  font-size: 24px;
  line-height: 1.3;

}
.project__img {
  height: 70%;
  border-radius: 10px;
  object-fit: cover;
  width: 100%;
}





