}

.header__open-menu {
  display: none;
}
@media screen and (max-width: 768px) {
  .header__open-menu {
    display: block;
    position: relative;
    margin: auto;
    left: 130px;
    bottom: 27px;
    z-index: 3;
    width: 25px;
    height: 20px;
  }
  .header__open-menu.span {
    position: absolute;
    background-color: white;
    margin: auto;
    width: 100%;
    height: 2px;
    top: 9px;
    transition: all 0.3s ease 0s;
  }
  .header__open-menu.active.span {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  .header__open-menu::before,
  .header__open-menu::after {
    content: '';
    background-color: white;
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    transition: all 0.3s ease 0s;
  }
  .header__open-menu::before {
    top: 0;
  }
  .header__open-menu::after {
    bottom: 0;
  }
  .header__open-menu.active::before {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    top: 9px;
  }
  .header__open-menu.active::after {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    bottom: 9px;
  }
}

@media screen and (max-width: 769px) {
  .header__menu {
    position: fixed;
    left: 0;
    top: -110%;
    width: 100%;
    height: 100%;
    z-index: 3;
    background-image: linear-gradient(to top, #060c2d 0%, #000000 100%);
    padding: 55px 0 0 0;
    transition: all 0.5s ease 0s;
    overflow: auto;
  }
  .header__menu.active {
    top: 50px;
  }
}


